<template>
    <div id="global-uploader">

        <!--  -->
        <uploader
                ref="uploader"
                :options="options"
                :autoStart="false"
                @file-added="onFileAdded"
                @file-success="onFileSuccess"
                @file-progress="onFileProgress"
                @file-error="onFileError"
                class="uploader-app">
            <uploader-unsupport></uploader-unsupport>

            <uploader-btn id="global-uploader-btn" :attrs="attrs" ref="uploadBtn"></uploader-btn>

            <uploader-list>
                <div class="file-panel" slot-scope="props">
                    <ul class="file-list">
                        <li v-for="file in props.fileList" :key="file.id" class="file-item">
                            <img src="@/common/images/icon_topic.png" alt="">
                            <div class="title">{{file.name}}</div>
                            <div class="desc">{{uploadText}}</div>
                            <!-- <uploader-file :class="'file_' + file.id" ref="files" :file="file" :list="true"></uploader-file> -->
                        </li>
                        <uploader-btns :hasBorder="hasBorder" @upload='handleUploader'></uploader-btns>
                    </ul>
                </div>
            </uploader-list>
        </uploader>

    </div>
</template>
<style lang="scss">
  .close {
    width: 10px;
    height: 10px;
  }
</style>

<script>
    /**
     *   
     *   Bus.$emit('openUploader', {}) 
     *   Bus.$on('fileAdded', fn); 
     *            Bus.$on('fileSuccess', fn); 
     */

import {ACCEPT_CONFIG} from '../common/config.js';
import Storage from 'good-storage';
import Bus from '../common/bus.js';
import SparkMD5 from 'spark-md5';
import $ from 'jquery';
import UploaderBtns from '@/views/uploader/UploaderBtn';

export default {
    props: {
        hasBorder: {
            type: Boolean,
            default: false
        }   
    },
    data() {
        return {
            options: {
                target: '/v1api/planupload',
                chunkSize: '2048000',
                fileParameterName: 'fileData',
                maxChunkRetries: 3,
                testChunks: true,   //
                // 
                checkChunkUploadedByResponse: function (chunk, message) {
                    let objMessage = JSON.parse(message);
                    if (objMessage.skipUpload) {
                        return true;
                    }

                    return (objMessage.uploaded || []).indexOf(chunk.offset + 1) >= 0
                },
                headers: {
                    // Authorization: Ticket.get() && "Bearer " + Ticket.get().access_token
                },
                query() {
                
                }
            },
            attrs: {
                accept: ACCEPT_CONFIG.getAll()
            },
            panelShow: false,   //panel
            collapse: false,
            uploadText: '  ',
            process: '0'
        }
    },   
    mounted() {
        Bus.$on('openUploader', query => {
            let data = query || {};
            this.params = Object.assign({}, data, {UUS: Storage.get('token')});
            console.log(query)
            if (this.$refs.uploadBtn) {
                console.log(12345);
                $('#global-uploader-btn').click();
            }
        });
    },
    computed: {
        //Uploader
        uploader() {
            return this.$refs.uploader.uploader;
        }
    },
    methods: {
        handleUploader() {
            this.$emit('upload')
        },
        onFileAdded(file) {
            this.panelShow = true;
            this.computeMD5(file);

            Bus.$emit('fileAdded');
        },
        onFileProgress(rootFile, file, chunk) {
            this.uploadText = `  ${Math.floor(Math.random()*70)}%`;
            console.log(`${file},,,,, ${file.name}chunk${chunk.startByte / 1024 / 1024} ~ ${chunk.endByte / 1024 / 1024}`)
        },
        onFileSuccess(rootFile, file, response, chunk) {
            let res = JSON.parse(response);
            // 200Uploader
            if (!res.result) {
                console.log(123);
                // this.$message({ message: res.message, type: 'error' });
                // // 
                // this.statusSet(file.id, 'failed');
                // return
            }

            // 
            if (res.needMerge) {
                // 
                this.statusSet(file.id, 'merging');
                // api.mergeSimpleUpload({
                //     tempName: res.tempName,
                //     fileName: file.name,
                //     ...this.params,
                // }).then(res => {
                //     // 
                //     Bus.$emit('fileSuccess');

                //     this.statusRemove(file.id);
                // }).catch(e => {});

            // 
            } else {
                Bus.$emit('fileSuccess');
                console.log('ssss');
                this.uploadText = '';
            }
        },
        onFileError(rootFile, file, response, chunk) {
            // this.$message({
            //     message: response,
            //     type: 'error'
            // })
            console.log('error');
        },

        /**
         * md5
         * @param file
         */
        computeMD5(file) {
            let fileReader = new FileReader();
            let time = new Date().getTime();
            let blobSlice = File.prototype.slice || File.prototype.mozSlice || File.prototype.webkitSlice;
            let currentChunk = 0;
            const chunkSize = 10 * 1024 * 1000;
            let chunks = Math.ceil(file.size / chunkSize);
            let spark = new SparkMD5.ArrayBuffer();

            // "MD5"
            this.statusSet(file.id, 'md5');
            file.pause();

            loadNext();

            fileReader.onload = (e => {
                spark.append(e.target.result);

                if (currentChunk < chunks) {
                    currentChunk++;
                    loadNext();

                    // MD5
                    this.$nextTick(() => {
                        $(`.myStatus_${file.id}`).text(((currentChunk/chunks)*100).toFixed(0)+'%')
                    })
                } else {
                    let md5 = spark.end();
                    this.computeMD5Success(md5, file);
                    console.log(`MD5${file.name} \nMD5${md5} \n${chunks} :${file.size} ${new Date().getTime() - time} ms`);
                }
            });

            fileReader.onerror = function () {
                this.error(`${file.name}`)
                file.cancel();
            };

            function loadNext() {
                let start = currentChunk * chunkSize;
                let end = ((start + chunkSize) >= file.size) ? file.size : start + chunkSize;

                fileReader.readAsArrayBuffer(blobSlice.call(file.file, start, end));
            }
        },

        computeMD5Success(md5, file) {
            // uploaderopts
            Object.assign(this.uploader.opts, {
                query: {
                    ...this.params,
                }
            })

            file.uniqueIdentifier = md5;
            file.resume();
            this.statusRemove(file.id);
        },

        fileListShow() {
            let $list = $('#global-uploader .file-list');

            if ($list.is(':visible')) {
                $list.slideUp();
                this.collapse = true;
            } else {
                $list.slideDown();
                this.collapse = false;
            }
        },
        close() {
            this.uploader.cancel();

            this.panelShow = false;
        },

        /**
         * : 'md5''transcoding''failed'
         * @param id
         * @param status
         */
        statusSet(id, status) {
            let statusMap = {
                md5: {
                    text: 'MD5',
                    bgc: '#fff'
                },
                merging: {
                    text: '',
                    bgc: '#e2eeff'
                },
                transcoding: {
                    text: '',
                    bgc: '#e2eeff'
                },
                failed: {
                    text: '',
                    bgc: '#e2eeff'
                }
            }

            this.$nextTick(() => {
                $(`<p class="myStatus_${id}"></p>`).appendTo(`.file_${id} .uploader-file-status`).css({
                    'position': 'absolute',
                    'top': '0',
                    'left': '0',
                    'right': '0',
                    'bottom': '0',
                    'zIndex': '1',
                    'backgroundColor': statusMap[status].bgc
                }).text(statusMap[status].text);
            })
        },
        statusRemove(id) {
            this.$nextTick(() => {
                $(`.myStatus_${id}`).remove();
            })
        },

        error(msg) {
            this.$notify({
                title: this.$t('c.false'),
                message: msg,
                type: 'error',
                duration: 2000
            })
        }
    },
    watch: {},
    destroyed() {
        Bus.$off('openUploader');
    },
    components: {
        UploaderBtns,
    }
}
</script>

