<template>
  <div class="uploader-container">
    <nav-bar page="second"></nav-bar>
    <div class="content">
      <div class="body">
        <div class="desc-intro">PDF / PPT / WORD / MP4</div>
        <global-uploader :hasBorder="hasBorder" @upload='handleUploader'></global-uploader>
      </div>
    </div>
    <bottom-nav-bar :step="step" :next="next" :complete="complete"></bottom-nav-bar>
  </div>
</template>
<script>
import NavBar from '@/components/NavBar.vue';
import BottomNavBar from '@/components/BottomNavBar.vue';
import Bus from '@/common/bus';
import GlobalUploader from '@/components/GlobalUploader.vue'
export default {
  components: {
    NavBar,
    BottomNavBar,
    GlobalUploader
  },
  data() {
    return {
      step: 3,
      next: false,
      complete: false,
      hasBorder: true,
      dir: '',
      taskType: '',
      taskName: ''
    }
  },
  activated() {
    let {dir} = this.$route.query;
    this.dir = dir;
  },
  mounted() {
    // 
    Bus.$on('fileAdded', () => {

    });

    // 
    Bus.$on('fileSuccess', () => {
      this.complete = true;
    });
  },
  methods: {
    handleUploader() {
      this.hasBorder = true;
      let data = {};
      data.taskType = this.dir;
      if(this.dir === 'jd') {
        data.taskName = '';
      } else if(this.dir === 'factory') {
        data.taskName = '';
      } else if(this.dir === 'safe') {
        data.taskName = '';
      } else {
        data.taskName = '';
      }
      Bus.$emit('openUploader', data);
    }
  }
}
</script>
